@import url(https://fonts.googleapis.com/css2?family=Chakra+Petch:ital,wght@0,400;0,500;0,600;1,600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
    width: 100%;
    height: 100vh;
    background-image: url(/todo_w_redux/static/media/bg.e6e7cd78.png);
    background-repeat: no-repeat;
    background-size: cover;
    color: white;
    position: relative;
}
.bg {
    width: 40%;
    height: auto;
    position: absolute;
    top: 60%;
    left: 50%;
    transform: translate(-50%, -50%);
}
.bg .main {
    text-align: center;
}
.bg .main h2 {
    font-size: 30px;
    padding: 10px 0;
    font-weight: bold;
    font-family: 'chakra petch';
    font-style: italic;
}
.bg .main h2 span {
    font-style: normal;
}

.bg .form {
    width: 70%;
    margin: 10px auto;
}
.bg .form input {
    width: 100%;
    height: 30px;
    border: none;
    background: #fefee4;
    font-family: 'chakra petch';
    border-radius: 10px;
    
}
.bg .form input:focus {
    outline: none;
}

.bg .list {
    width: 70%;
    margin: 0 auto;
    font-size: 16px;
    height: 180px;
    overflow-y: auto;
    overflow-x: hidden;
}
.bg .list li {
    width: 100%;
    display: flex;
    justify-content: space-between;
    height: 32px;
    line-height: 32px;
    font-family: 'chakra petch';
}
.bg .list li input {
    line-height: 32px;
    margin-top: 9px;
}

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
